___CSS_LOADER_EXPORT___.push([module.id, "\n.container[data-v-43466965] {\n    padding: 32px 20px 20px;\n}\n.avatarLogo[data-v-43466965] {\n    vertical-align: middle;\n    width: 30px;\n    height: 30px;\n    border-radius: 50%;\n}\n.ExportBtn[data-v-43466965] {\n    border: unset;\n    background: unset;\n    font-style: normal;\n    font-weight: bold;\n    font-size: 16px;\n    letter-spacing: 0.02px;\n    padding: 1px 6px 1px 0;\n}\n.VueTables__sort-icon[data-v-43466965]{\n    float: right;\n    font-size: 15px;\n    font-weight: 600;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/application/modelVersionDialog.vue"],"names":[],"mappings":";AAwGA;IACI,uBAAuB;AAC3B;AACA;IACI,sBAAsB;IACtB,WAAW;IACX,YAAY;IACZ,kBAAkB;AAEtB;AAEA;IACI,aAAa;IACb,iBAAiB;IACjB,kBAAkB;IAClB,iBAAiB;IACjB,eAAe;IACf,sBAAsB;IACtB,sBAAsB;AAC1B;AACA;IACI,YAAY;IACZ,eAAe;IACf,gBAAgB;AACpB","sourcesContent":["\n<template>\n     <div class=\"container\">\n         <div v-if=\"histories\" class=\"vue-table\">\n            <v-client-table :data=\"histories\" :columns=\"appDatatableColumns\" :options=\"optionsDataTable\">\n                <template #createDate=\"props\">\n                    <span>{{new Intl.DateTimeFormat(\"tr-TR\",options).format(new Date(props.row.createDate))}}</span>\n                </template>\n                <template #updateDate=\"props\">\n                    <span>{{ new Intl.DateTimeFormat(\"tr-TR\",options).format(new Date(props.row.updateDate)) }}</span>\n                </template>\n                <template #actions=\"props\">\n                    <button class='ExportBtn'\n                    :style=\"{ 'color':parseInt(props.row.version) == computedVersion() ? '#A1A1A1' : '#151233'}\"\n                    @click=\"selectVersion(props.row.version)\">{{ parseInt(props.row.version) == computedVersion() ?\n                            'Selected' : 'Select'\n                        }}</button>\n                </template>\n            </v-client-table>\n        </div>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { container } from 'tsyringe';\nimport { ref } from 'vue';\nimport { IModelHistoryInfo } from '../../../../domain/model/models';\nimport { IListModelVersions } from '../../../../domain/useCase/IListModelVersions';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport { ILocalization } from \"../../../../domain/infrastructure/ILocalization\";\nconst props = defineProps<IModelVersionProp>();\nconst histories = ref<Array<IModelHistoryInfo>>([])\nconst selectedModel = ref(props.clickedNode);\nlet allversion: any = [];\nconst appDatatableColumns = ref(['name', 'createDate', 'createdBy', 'updateDate', 'updatedBy', 'shortComment', 'version', 'actions']);\nconst localization = container.resolve<ILocalization>(ILocalization)\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nlet options :any = { month:'2-digit',day:'2-digit', year:'numeric', hour: 'numeric', minute: 'numeric',second: 'numeric'};\nconst optionsDataTable = ref({\n    filterable: false, // omit the `id` column,\n    headings: {\n        name: localization.get('exportName'),\n        createDate: localization.get('exportcreateDate'),\n        createdBy: localization.get('exportcreatedBy'),\n        updateDate: localization.get('exportupdateDate'),\n        updatedBy: localization.get('exportupdatedBy'),\n        shortComment: localization.get('exportshortComment'),\n        version: localization.get('exportversion'),\n    },\n    skin: 'version-table',\n    dateColumns: ['createDate', 'updateDate'],\n        // sortIcon:{\n        //     base:'mdi',\n        //     is:'mdi mdi-unfold-less-horizontal',\n        //     up: 'mdi mdi-arrow-up',\n        //     down: 'mdi mdi-arrow-down'\n        //   },\n    texts: {\n        count: '',\n        noResults: \"Saved version could not be found in the system, the last saved model of the file will be released.\"\n    }\n});\n\n\n\ninterface ISelectedModel {\n    id: string,\n    state: { checked: boolean },\n    text: string,\n    type: string,\n    version: string\n}\ninterface IModelVersionProp {\n    clickedNode: ISelectedModel,\n    versions?:Array<IModelHistoryInfo>,\n    cb?:(version : string) => void;\n}\n\nif(props.versions) {\n    histories.value = props.versions;\n} else {\n    executor.execute(async () => {\n        histories.value = await executor.executeUseCase<IListModelVersions>(IListModelVersions, { modelID: selectedModel.value.id, versionType: \"major\" });\n        allversion = histories.value.map(item => parseInt(item.version!));\n    }, { loading: true });\n}\n\n\nfunction computedVersion() {\n    if (selectedModel.value.version != 'System latest') {\n        return selectedModel.value.version\n    }\n    else {\n        return Math.max(...allversion)\n    }\n\n}\nfunction selectVersion(n: string) {\n    selectedModel.value.version = n\n    props.cb && props.cb(n);\n}\n</script>\n<style scoped>\n@import url(\"../../../../styles/IDE3.css\");\n.container {\n    padding: 32px 20px 20px;\n}\n.avatarLogo {\n    vertical-align: middle;\n    width: 30px;\n    height: 30px;\n    border-radius: 50%;\n\n}\n\n.ExportBtn {\n    border: unset;\n    background: unset;\n    font-style: normal;\n    font-weight: bold;\n    font-size: 16px;\n    letter-spacing: 0.02px;\n    padding: 1px 6px 1px 0;\n}\n.VueTables__sort-icon{\n    float: right;\n    font-size: 15px;\n    font-weight: 600;\n}\n</style>"],"sourceRoot":""}]);
